import React from 'react';
import { MDXRenderer } from "gatsby-plugin-mdx"

import {
  Card,
  CardContent,
  Typography,
  IconButton,
  Box,
  Chip,
  AppBar,
  Toolbar,
} from '@mui/material';
import {
  NavigateNext as NextIcon,
  NavigateBefore as PrevIcon,
  Close as CloseIcon,
  Fullscreen as FullscreenIcon,
} from '@mui/icons-material';

const LyricsViewer = ({
  currentSong,
  currentSongIndex,
  totalSongs,
  onNext,
  onPrev,
  onClose,
  onFullscreen,
  hasNext,
  hasPrev,
}) => {
  if (!currentSong) {
    return (
      <Card sx={{ textAlign: 'center', p: 4 }}>
        <Typography variant="h6" color="text.secondary" gutterBottom>
          Mi Lista de Letras
        </Typography>
        <Typography color="text.secondary">
          Agrega canciones para ver sus letras en orden
        </Typography>
      </Card>
    );
  }

  return (
    <Card sx={{ mb: 2 }}>
      {/* Header con controles de navegacin */}
      <AppBar position="static" color="transparent" elevation={1}>
        <Toolbar variant="dense">
          <Box sx={{
            flexGrow: 1,
            ml: 1,
            // maxWidth: { xs: 80, sm: 'none' }
          }}>
            <Typography variant="body"
              sx={{
                // maxWidth: { xs: 80, sm: 'none' }
            }}
            >
              {currentSong.title}
            </Typography>
          </Box>

          <Chip 
            label={`${currentSongIndex + 1} / ${totalSongs}`}
            size="small"
            color="primary"
            sx={{ mr: 1 }}
          />

          <IconButton onClick={onFullscreen} size="small">
            <FullscreenIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      {/* Controles de navegacin */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', px: 2, py: 1 }}>
        <IconButton color="primary" onClick={onPrev} disabled={!hasPrev}>
          <PrevIcon />
        </IconButton>
        
        <Typography variant="body1" color="text.secondary" onClick={hasPrev ? onPrev : null} disabled={!hasPrev}>
          {hasPrev ? 'Anterior' : 'Inicio'} 
        </Typography>
          
        <Typography variant="body1" color="text.secondary" onClick={hasNext ? onNext : null} disabled={!hasNext}>
           {hasNext ? 'Siguiente' : 'Fin'}
        </Typography>


        
        <IconButton color="primary" onClick={onNext} disabled={!hasNext}>
          <NextIcon />
        </IconButton>
      </Box>

      {/* Letra de la cancin - Renderizado MDX */}
      <CardContent>
        <Box
          sx={{
            minHeight: 400,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            p: 2,
          }}
        >
          {currentSong.body ? (
            
            <Typography
              // component="pre"
              variant="body"
              component="body"
              sx={{
                whiteSpace: 'pre-wrap',
                textAlign: 'center',
                fontFamily: 'inherit',
                // fontSize: '1.1rem',
                // lineHeight: 1.8,
                width: '100%',
                margin: "0",
                '&.MuiTypography-root h3': {
                margin: "2px 0 1px 0",
                // fontSize: { xs: '1.2rem', md: '1.5rem' },
                },
              '&.MuiTypography-root p': {
                margin: "0",
                padding: '0 2px'
                },
              '& strong': {
                display: "flex",
                justifyContent: "center",
                margin: "40px 0 15px 0",
                // padding: "15px 0",
                },
              '& em': {
                justifyContent: "center",
                // margin: "40px 0",
                padding: "20px 15px",
                },
              }}
            >
              {currentSong.body}
            </Typography>
          ) : (
            <Typography color="text.secondary">
              No hay letra disponible para esta cancin
            </Typography>
          )}
  


        </Box>
      </CardContent>
    </Card>
  );
};

export default LyricsViewer;
